APP.MEMBER.POP_INFO_ADMIN = function(mem_idx) {
    if( typeof mem_idx == 'undefined' || ! mem_idx  ) {
        alert(' .');
        return false;
    }

    APP.MODAL.open({
        width: 800,
        height :600,
        header : {
            title : ' '
        },
        callback : function(){
            location.reload();
        },
        iframe : {
            method : 'get',
            url : '/admin/members/info/' + mem_idx,
            param : {}
        }
    });

};

